import React, { Component } from 'react';

const clientId= "ca6fee0f88df528c206afa9b4d1dacdbc29febc2684aee9ece11f69dacaa7459";
const endpoint = 'https://api.unsplash.com/search/photos';


class App extends Component {
  constructor(props){
    super(props);
    this.state ={
      queries: [],
      query: "random",
      inputValue: "",
    }
  }



  handleImageSave(imgid){
      const updSavedImages = this.state.savedImages.slice()
      updSavedImages[updSavedImages.length] = imgid
      this.setState({
        savedImages: updSavedImages
      })
  }

  handleQueries(newQuery){
    const updatedQueries = this.state.queries.slice();
    updatedQueries.push(newQuery)
    this.setState({
      queries: updatedQueries
    })
  }
  handleInputValue(e){
    this.setState({
      inputValue: e.target.value
    })
  }

  handleQuery(){
    const updatedQueries = this.state.queries.slice();
    updatedQueries.push(this.state.inputValue);
    this.setState({
      query: this.state.inputValue,
      queries: updatedQueries

    })
  }

  handleTagQuery(e){
  const newQuery =  e.target.value;
  this.setState({
    query: newQuery
  })
  }

  render() {
    return (

      <div className="App">
        <div className="grid-container">
            <div className="imageSearch grid-item">
              <SearchField value={this.state.inputValue} onValueChange={(e) => this.handleInputValue(e)}></SearchField>
            </div>
            <div className="actionMenu grid-item">
              <div className="actionMenu-container">
                  <button className="action-container-item action-button action-button-search" onClick={()=>this.handleQuery()}>
                    <span>Search</span>
                  </button>
            </div>
            </div>
            <div className="imagePlacard grid-item">
                <h2>Results Placard</h2>
                <ImagePlacard query={this.state.query}></ImagePlacard>
            </div>
            <div className="imageSaved grid-item">
                <h2>Saved Queries</h2>
                {this.state.queries.map(
                  (value) =>
                  <button className="keyword" value={value} onClick={(e) => this.handleTagQuery(e)}>
                  <span>{value}</span>
                  </button>
                )}

            </div>
        </div>

    </div>
    );
  }
}



class SearchField extends Component{

  render(){

    return(
      <div>
        <span className="fa fa-search"></span>
        <input className="input-field searchField" placeholder="Search for Images" value={this.props.value} onChange={this.props.onValueChange}/>
      </div>
    )
  }

}

class ImagePlacard extends Component{
  constructor(props){
    super(props);
    this.state = {
      isClicked: true,
      pictures: [],
    }
  }


  componentDidMount(){
    console.log("I have loaded")
    const req = endpoint + "?query=" + this.props.query + "&client_id=" + clientId;
    const addedPicturesArray = [];
    fetch(req)
    .then(res => {
      return res.json();
    }).then(resJson => resJson.results.map((pic) => {
      addedPicturesArray.push({id: pic.id, image: pic.urls.regular})
      this.setState({
        pictures: addedPicturesArray
      })
    })
    )

  }

  componentDidUpdate(prevProps){
    if (this.props.query !== prevProps.query){
      console.log("Update has loaded")
      const req = endpoint + "?query=" + this.props.query + "&client_id=" + clientId;
      const addedPicturesArray = [];
      fetch(req)
      .then(res => {
        return res.json();
      }).then(resJson => resJson.results.map((pic) => {
        addedPicturesArray.push({id: pic.id, image: pic.urls.regular})
        this.setState({
          pictures: addedPicturesArray
        })
      })
      )
    }
  }

  render(){
    return (
      <div className="imagePlacards-container">
        {this.state.pictures.map(
          (picture) =>
            <div className="image-item-container">
              <div key={picture.id}>
                <img className="image-item" src={picture.image}></img>
              </div>
              <button className='image-item-zoom'>
                <i className="fa fa-search-plus" aria-hidden="true"></i>
              </button>
              <button className='image-item-save' id={picture.id}>
                <i className="fa fa-plus"></i>
              </button>
          </div>
        )}

      </div>
    );
  }
}


class ImageSaved extends Component{
  render(){
    return(
        <div className="imageSaved-container">
          {this.props.images.map(
            (id) =>
            <div className="imageSaved-item-container">
              <img className="imageSaved-item" id={id} src={"./images/cars/" + id + ".jpg"}></img>
              <button className='image-item-zoom'>
                <i className="fa fa-search-plus" aria-hidden="true"></i>
              </button>
              <button className='image-item-save' id={id}>
                <i className="fa fa-times"></i>
              </button>
            </div>

          )
          }

        </div>
    )
  }
}


export default App;

